import {CenterActionTypes} from "./center.types";

const INITIAL_STATE = {
    centers: [],
    tracking_centers: []
}

const centerReducer = (state = INITIAL_STATE, action = "") => {
    switch (action.type) {
        case CenterActionTypes.SET_CENTERS:
            return {
                ...state,
                centers: action.payload
            };
        case CenterActionTypes.SET_TRACKING_CENTER:
            return {
                ...state,
                tracking_centers: action.payload
            }
        default:
            return state;
    }
}
export default centerReducer;